import React, { useState, useEffect, useContext, Fragment } from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import { toast } from 'react-toastify';
import { Button, Form, Row, Col, FormGroup, Input, CustomInput, Label } from 'reactstrap';
import Divider from '../common/Divider';
import SocialAuthButtons from './SocialAuthButtons';
import withRedirect from '../../hoc/withRedirect';
import AppContext from '../../context/Context';
import axios from 'axios';
import { tokensApi } from '../../config';
import Loader from '../common/Loader';
import moment from 'moment';

const LoginForm = ({ setRedirect, hasLabel, layout }) => {
  // State
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [remember, setRemember] = useState(true);
  const [isDisabled, setIsDisabled] = useState(true);
  const [authenticating, setAuthenticating] = useState(false);
  // Context
  const { loggedIn, setLoggedIn } = useContext(AppContext);
  const { authToken, setAuthToken } = useContext(AppContext);
  // const { lastLoginTime, setLastLoginTime } = useContext(AppContext);

  console.log(">>> authToken >>>", authToken);

  // if (loggedIn && authToken) {
  //   setRedirect(true);      
  // }  

  // if (lastLoginTime) {
  //   // if the last login time is past 24 hours. reset the token by forcing the user to log back in
  //   console.log(">>> LoginForm >>> lastLoginTime >>> ", lastLoginTime);
  //   const currentTime = moment.format();
  //   const hours = (currentTime.diff(lastLoginTime, 'hours'));
  //   console.log(">>> hours >>> ", hours);
  //   // token is good for 24 hours
  //   if (hours < 24) {
  //     if (loggedIn && authToken) {
  //       setRedirect(true);      
  //     }
  //   }
  // }

  const authenticateUser = async () => {
    let result;
    setAuthenticating(true);
    if (!username) {
      toast.error("Please enter your Username to login");
    }
    if (!password) {
      toast.error("Please enter your Password to login");
    }
    axios.post(tokensApi, {
      withCredentials: true,
      headers: {
        "Accept": "application/json",
        "Content-Type": "application/json"
      }
    },{
      auth: {
        username: username,
        password: password
    }}).then(function(response) {
      console.log('Authenticated');
      toast.success(`Logged in as ${username}`);
      const token = (response && response.data && response.data.token) ? response.data.token : "";
      if (token) {
        setAuthenticating(false);
        setLoggedIn(true);
        setAuthToken(token);
        // const loginTime = moment().format(); 
        // console.log(">>> authenticateUser >>> loginTime >>> ", loginTime);
        // setLastLoginTime(loginTime);
        setRedirect(true);      
      }
    }).catch(function(error) {
      setAuthenticating(false);
      console.log('Error on Authentication: ', error);
      toast.error(`Login Failed: Unauthorized - ${error}`);
    });
  };
  
  const handleSubmit = async(e) => {
    e.preventDefault();
    const result = await authenticateUser();
  };

  useEffect(() => {
    setIsDisabled(!username || !password);
  }, [username, password]);

  return (
    <Fragment>
      {authenticating ? (
        <Loader />
      ) : (
        <Form onSubmit={handleSubmit}>
          <FormGroup>
            {hasLabel && <Label>Username</Label>}
            <Input
              placeholder={!hasLabel ? 'Username' : ''}
              value={username}
              onChange={({ target }) => setUsername(target.value)}
              type="text"
            />
          </FormGroup>
          <FormGroup>
            {hasLabel && <Label>Password</Label>}
            <Input
              placeholder={!hasLabel ? 'Password' : ''}
              value={password}
              onChange={({ target }) => setPassword(target.value)}
              type="password"
            />
          </FormGroup>
          <Row className="justify-content-between align-items-center">
            <Col xs="auto">
              <CustomInput
                id="customCheckRemember"
                label="Remember me"
                checked={remember}
                onChange={({ target }) => setRemember(target.checked)}
                type="checkbox"
              />
            </Col>
            <Col xs="auto">
              <Link className="fs--1" to={`/authentication/${layout}/forget-password`}>
                Forget Password?
              </Link>
            </Col>
          </Row>
          <FormGroup>
            <Button color="primary" block className="mt-3" disabled={isDisabled}>
              Log in
            </Button>
          </FormGroup>
          {/* <Divider className="mt-4">or log in with</Divider>
          <SocialAuthButtons /> */}
        </Form>
      )}
    </Fragment>
  );
};

LoginForm.propTypes = {
  setRedirect: PropTypes.func.isRequired,
  layout: PropTypes.string,
  hasLabel: PropTypes.bool
};

LoginForm.defaultProps = {
  layout: 'basic',
  hasLabel: false
};

export default withRedirect(LoginForm);
